.movieContainer {
	display: -ms-flexbox;
	display: flex;
	padding: 5px;
}

.movieImage, .movieDescription {
	width: 50%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.movieImage img {
	width: 100%;
}

.movieDescription {
	padding: 10px;
}

.addComment {
	width: 50%;
    padding: 0 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
}

.addComment textarea {
	width: 100%;
}

.userComment {
	border-bottom: 1px solid;
}

.userComment_header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}

.userComment_header p:first-child {
	font-weight: bold;
}

.userComment_header p:last-child {
	font-size: 10px;
}

.userComment_text {
	font-style: italic;
}



